<template>
    <div :id="element.uuid">
        <v-container>
            <v-tooltip bottom color="success" :disabled="isTooltip" z-index="10">
                <template v-slot:activator="{ on, attrs }">
                    <v-card outlined :color="minimaptoolbar ? null : colorToolbar" v-bind="attrs" v-on="on">
                        <v-toolbar v-if="!isDatailView" :color=colorToolbar dark hide-on-scroll height="30px" class="drag-handle">
                            <v-hover v-if="minimaptoolbar" v-slot="{ hover }">
                                <v-btn icon @click="showDataConstr">
                                    <v-icon>{{ iselementOpenClose ? (hover? 'mdi-chevron-double-left' :'mdi-chevron-double-right') : (hover? 'mdi-chevron-double-right' :'mdi-chevron-double-left')}}</v-icon>
                                </v-btn>
                            </v-hover>
                            <v-btn v-if="minimaptoolbar" icon @click.stop="dialogPath=true">
                                <v-icon> mdi-routes</v-icon>
                            </v-btn>
                            <dialogPathSetting v-model="dialogPath" :path="element.path" @submit="submitDialog"/>
                            <v-toolbar-title>Data Constr</v-toolbar-title>
                            <v-spacer></v-spacer>
                            <v-btn v-if="minimaptoolbar" icon @click="viewARXML">
                                <v-icon> mdi-format-text</v-icon>
                            </v-btn>
                        </v-toolbar>
                        <v-toolbar v-else hide-on-scroll dense flat>
                            <v-toolbar-title>Data Constr</v-toolbar-title>
                        </v-toolbar>
                        <v-card-text v-if="iselementOpenClose">
                            <v-text-field v-model="element.name" :label="'name  <'+element.path +'>'" :rules="rules.name" placeholder="String" style="height: 45px;" class="lable-placeholer-color"
                                        @input='inputDateConstrName' outlined dense></v-text-field>
                            <v-text-field v-model="element.lowerlimit" label="Lower Limit" placeholder="" style="height: 45px;"  outlined dense class="lable-placeholer-color"></v-text-field>
                            <v-text-field v-model="element.upperlimit" label="Upper Limit" placeholder="" style="height: 45px;"  outlined dense class="lable-placeholer-color"></v-text-field>

                        </v-card-text>
                        <v-card-text v-else>
                            <v-text-field v-model="element.name" :label="'name  <'+element.path +'>'" :rules="rules.name" placeholder="String" style="height: 45px;" class="lable-placeholer-color"
                                        readonly outlined dense></v-text-field>
                        </v-card-text>
                    </v-card>
                </template>
               <span>{{ element.name }}</span>
            </v-tooltip>
            <v-dialog v-model="dialogText" persistent scrollable width="800">
                <v-card >
                    <v-card-title class="text-h6 green accent-1"> Edit Text 
                        <v-spacer></v-spacer>
                        <v-btn @click="saveARXML()" icon color="green darken-1" x-samll dark>
                            <v-icon>mdi-content-save</v-icon>
                        </v-btn>
                        <v-btn @click="cancelARXML()" icon color="green darken-1" x-samll dark>
                            <v-icon>mdi-close</v-icon>
                        </v-btn>
                    </v-card-title>
                    <v-card-text>
                        <br>
                        <v-row>
                            <label style="padding:10px;">&#60;SHORT-NAME&#62;</label>
                            <v-text-field v-model="editARXML.name" placeholder="String" style="height: 15px;" class="lable-placeholer-color" dense></v-text-field>
                            <label style="padding:10px;">&#60;&#47;SHORT-NAME&#62;</label>
                        </v-row>
                        <v-row>
                            <label style="margin:1px 10px 1px;">&#60;DATA-CONSTR-RULES&#62;</label>
                        </v-row>
                        <v-row>
                            <label style="margin:1px 10px 1px 30px;">&#60;DATA-CONSTR-RULE&#62;</label>
                        </v-row>
                        <v-row>
                            <label style="margin:1px 10px 1px 60px;">&#60;INTERNAL-CONSTRS&#62;</label>
                        </v-row>
                        <v-row>
                            <label style="padding:10px; margin-left:90px;">&#60;LOWER-LIMIT&#62;</label>
                            <v-text-field v-model="editARXML.lowerlimit" placeholder="Time" style="height: 15px;" class="lable-placeholer-color" dense></v-text-field>
                            <label style="padding:10px;">&#60;&#47;LOWER-LIMIT&#62;</label>
                        </v-row>
                        <v-row>
                            <label style="padding:10px; margin-left:90px;">&#60;UPPER-LIMIT&#62;</label>
                            <v-text-field v-model="editARXML.upperlimit" placeholder="Time" style="height: 15px;" class="lable-placeholer-color" dense></v-text-field>
                            <label style="padding:10px;">&#60;&#47;UPPER-LIMIT&#62;</label>
                        </v-row>
                        <v-row>
                            <label style="margin:1px 10px 1px 60px;">&#60;&#47;INTERNAL-CONSTRS&#62;</label>
                        </v-row>
                        <v-row>
                            <label style="margin:1px 10px 1px 30px;">&#60;&#47;DATA-CONSTR-RULE&#62;</label>
                        </v-row>
                        <v-row>
                            <label style="margin:1px 10px 1px;">&#60;&#47;DATA-CONSTR-RULES&#62;</label>
                        </v-row>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn class="d-inline-flex ml-3 mr-1" color="green darken-1" text  @click="saveARXML()" >
                            Save
                        </v-btn>
                        <v-btn class="d-inline-flex ml-3 mr-1" color="green darken-1" text @click="cancelARXML()">
                            Cancel
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-container>
    </div>
</template>

<script>
import constant from "../store/constants.js"
import dialogPathSetting from '../components/dialogPathSetting.vue'
import { EventBus } from "../main.js"

export default {
    props: ['element', 'isDatailView', 'minimaptoolbar'],
    components:{dialogPathSetting},
    computed: {
        activeUUID() {
            return this.$store.state.activeUUID
        },
        setting() {
            return this.$store.state.setting
        },
    },
    watch: {
        activeUUID(val) {
            this.setToolbarColor(val)
        },
        /*detailViewUUID(val) {
            this.setToolbarColorDetailView(val)
        },*/
        setting(value) {
            this.zoomvalue = value.zoomMain
            if (this.zoomvalue < this.$setZoominTooltip) {
                this.isTooltip = false
            } else {
                this.isTooltip = this.minimaptoolbar
            }
        },
    },
    created() {
        this.setToolbarColor(this.$store.state.activeUUID)
    },
    data() {
        return {
            rules: {
                name:  [val => (val || '').length > 0 ],
            },
            dialogPath : false,
            colorToolbar: "#6A5ACD",
            zoomvalue: this.$store.state.setting.zoomMain,
            isTooltip: this.minimaptoolbar,
            iselementOpenClose: true,//this.minimaptoolbar, //toolbar    true:    / false : toolbar 
            dialogText: false,
            editARXML: {name:'', lowerlimit: '', upperlimit: ''},
        }
    },
    mounted () {
        if (this.minimaptoolbar && this.zoomvalue < this.$setZoominElement) {
            this.isTooltip = false
        }
        EventBus.$on('Element-open', (isopen, uuid) => {
            if (this.element.uuid == uuid) {
                this.iselementOpenClose = isopen
            }
        })
    },
    methods: {
        submitDialog(element) {
            this.element.path = element
            this.$store.commit('changePathElement', {uuid:this.element.uuid, path: this.element.path, name: this.element.name} )
            this.$store.commit('isintoErrorList', {uuid:this.element.uuid, name:this.element.name, path:this.element.path})
        },
        setToolbarColor(uuid) 
        {
            if(this.element.uuid == uuid) {
                this.colorToolbar = "#FF1493" 
            } else {
                this.colorToolbar = "#6A5ACD"
            }
        },
        setToolbarColorDetailView(isdetail) {
            if(this.element.uuid == isdetail) {
                this.colorToolbar = "#B0E0E6" 
            } else if (this.element.uuid == this.$store.state.activeUUID) {
                this.colorToolbar  = "#FF1493"
            } else {
                this.colorToolbar = "#6A5ACD"
            }
        },
        showDataConstr () {
            this.iselementOpenClose = this.iselementOpenClose ? false : true
            if (this.iselementOpenClose) { //SWComponent RClient 3        
                this.$store.commit('setOpenCloseCompuDataStrMachineModeD', {
                                uuid: this.element.uuid, left: this.element.left, top: this.element.top,
                                beforElement: constant.Implementation_str, closeElement: constant.CompuMethod_str})
            }
            this.$nextTick(() => {
                EventBus.$emit('drawLine')
                document.getElementById(this.element.uuid+1).scrollIntoView({ behavior: 'smooth', block: 'start' })
            })
        },
        inputDateConstrName () {
            this.$store.commit('editDataConstr', {compo:"Name", uuid:this.element.uuid, name:this.element.name} )
            this.$store.commit('changePathElement', {uuid:this.element.uuid, path: this.element.path, name: this.element.name} )
            if (this.element.name != '') {
                this.$store.commit('isintoErrorList', {uuid:this.element.uuid, name:this.element.name, path:this.element.path})
            }
        },

        viewARXML() {
            this.editARXML.name = this.element.name
            this.editARXML.lowerlimit = this.element.lowerlimit
            this.editARXML.upperlimit = this.element.upperlimit
            this.dialogText= true
        },
        saveARXML() {
            if (this.element.name != this.editARXML.name) {
                this.$store.commit('editDataConstr', {compo:"Name", uuid:this.element.uuid, name:this.editARXML.name} )
                this.$store.commit('changePathElement', {uuid:this.element.uuid, path: this.element.path, name: this.editARXML.name} )
                if (this.editARXML.name != '') {
                    this.$store.commit('isintoErrorList', {uuid:this.element.uuid, name:this.editARXML.name, path:this.element.path})
                }
            }
            this.element.name = this.editARXML.name
            this.element.lowerlimit = this.editARXML.lowerlimit
            this.element.upperlimit = this.editARXML.upperlimit
            this.cancelARXML()
        },
        cancelARXML() {
            this.editARXML = {name:'', lowerlimit: '', upperlimit: ''}
            this.dialogText = false
        },

    },

}
</script>